.App {
  text-align: center;
  font-family: 'Rubik', sans-serif;
}

.blueScreen {
  opacity: 0;
  -webkit-transition:opacity 2s ease-in-out;
  -o-transition:opacity 2s ease-in-out;
  transition:opacity 2s ease-in-out;
  background: #81CFE0;
}
.blueScreen.visible {
  opacity: 1;
  top: 0;
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 11;
}

.App .appContainer {
  width: 100%;
  height: 100%
}



